<template>
    <div class="chapter">
        <div class="chapter__image" :style="style"></div>
        <div class="chapter__title __container">
            {{ title }}
        </div>

    </div>
</template>
 
<script>
export default {
    components: {},
    data() { },
    props: {
        title: {
            type: String
        },
        src: {
            type: String
        }
    },
    methods: {},
    computed: {
        style() {
            const style = "background-image: url('/image/" + this.src + "')"
            return style
        }
    }
}
</script>
 
<style scoped>
.chapter {
    width: 100%;

}

.chapter__image {
    width: 100%;
    background-position: center center;
    background-size: cover;
    max-height: 440px;
    height: 100vh;
    z-index: -2;
}
</style>